<template>
  <v-container :class="'text-center ' + themeColor">
    <v-progress-circular :size="70" :width="7" color="purple" indeterminate v-if="show == false"></v-progress-circular>
    <div v-else>
    <h3 :class="'text display-1 font-weight-bold ' + themeColor">{{ title }}</h3>
    <v-container>
      <v-sheet
        class="mx-auto"
        max-width="1000"
      >
      <horizontal-bar-chart :chartdata="chartdata" :options="options"></horizontal-bar-chart>
      </v-sheet>
    </v-container>
    <p :class="'text text-wrap ' + themeColor" style="width: 80%; margin: 0 auto;" v-html="text"></p>
    <hr :class="themeColor"/>
    </div>
  </v-container>
</template>

<script>
  import HorizontalBarChart from './HorizontalBarChart.js'

  export default {
    components: {
      HorizontalBarChart
    },
    mounted: function () {
      this.show = true
    },
    data: () => ({
      options: {
        show: false,
        responsive: true,
        maintainAspectRatio: false,
        scales: {
            xAxes: [{
                stacked: true,
                categoryPercentage: 0.5,
                ticks: {
                  min: -50,
                  max: 50,
                  callback: function(t) {
                    return t < 0 ? Math.abs(t) : t;
                  }
                }
            }],
            yAxes: [{
                stacked: true,
                categoryPercentage: 0.5
            }]
        },
        legend: {
            position: 'right',
            display: false,
        },
        tooltips: {
          callbacks: {
            label: function(tooltipItem, data) {
                var label = data.datasets[tooltipItem.datasetIndex].label || '';

                if (label) {
                    label += ': ';
                }
                label += Math.abs(Math.round(tooltipItem.xLabel * 100) / 100);
                return label;
            }
          }
        },
      }
    }),
    props: ['title', 'text', 'themeColor', 'sources'],
    computed: {
      // a computed getter
      chartdata: function () {
        var pos = []
        var neg = []
        const keys = Object.keys(this.sources)
        for (const key of keys) {
          pos.push(parseInt(this.sources[key].count_list.split(' ')[0]))
          neg.push(parseInt(this.sources[key].count_list.split(' ')[1]) * -1)
        }
        var dataChart = {
          labels: Object.keys(this.sources),
          datasets: [
            {
              label: 'Positive',
              backgroundColor: '#77dd77',
              data: pos
            },
            {
              label: 'Negative',
              backgroundColor: '#FF6961',
              data: neg
            }
          ]
        }
        return dataChart
      }
    }
  }
</script>