<template>
  <v-container class="text-center">
    <v-container v-if="show == false">
    <p>This may take a few minutes</p>
    <v-progress-circular :size="70" :width="7" color="amber" indeterminate></v-progress-circular>
    </v-container>
      <!-- <v-lazy v-else> -->
        <div v-else>
        <v-row>
            <v-col md='6' sm='12'>
              <LineTrends :title="overallTrend.title" :text="overallTrend.text" :themeColor="overallTrend.themeColor" :trend="tweets_trends"/>             
            </v-col>
            <v-col md='6' sm='12'>
              <LineTrendsV2 :title="senTrend.title" :text="senTrend.text" :themeColor="senTrend.themeColor" :trend="sentiment_trends"/>           
            </v-col>
        </v-row>
        <v-row>
            <v-col md='6' sm='12'>
                <OverviewWindow :newsCounter="daily_overall.news" :adsCounter="daily_overall.ads" :opiCounter="daily_overall.opinion" :overallCounter="daily_overall.total"/>
            </v-col>
            <v-col md='6' sm='12'>
                <ThreeBar :title="daily.title" :text="daily.text" :themeColor="daily.themeColor" :posneuneg="daily_overall.news + ' ' + daily_overall.ads + ' ' + daily_overall.opinion"/>
            </v-col>
        </v-row>
        </div>
      <!-- </v-lazy> -->
  </v-container>
</template>
<script>
import ThreeBar from './Analysis/ThreeBar'
import OverviewWindow from './Analysis/OverviewWindow'
import LineTrends from './Analysis/LineTrends'
import LineTrendsV2 from './Analysis/LineTrendsV2'
import { db } from '../utils/DB'

export default {
  name: 'Overview',
  components: {
    ThreeBar,
    OverviewWindow,
    LineTrends,
    LineTrendsV2
  },
  data: () => ({
    daily: {
      title: 'Daily Tweets',
      text: '',
      themeColor: 'no-color'
    },
    overallTrend: {
      title: 'Overall Trend',
      text: '',
      themeColor: 'no-color'
    },
    senTrend: {
      title: 'Sentiment Trend',
      text: '',
      themeColor: 'no-color'
    },
    overall: null,
    tweets_trends: null,
    sentiment_trends: null,
    daily_overall: null,
    show: false
  }),
  created () {
    this.$rtdbBind('overall', db.ref('overview')).then(() => {
        this.tweets_trends = this.overall.tweets_store
        this.daily_overall = this.overall.tweets_daily
        this.sentiment_trends = this.overall.sentiment_store
        this.show = true
      })
      .catch((error) => {
        console.log('error in loading: ', error)
      })
  },
}
</script>