<template>
  <v-container :class="'text-center ' + themeColor">
    <h3 :class="'text display-1 font-weight-bold ' + themeColor">{{ title }}</h3>
    <v-container>
      <v-sheet
        class="mx-auto"
        max-width="500"
      >
      <line-chart :chartdata="chartdata" :options="options"></line-chart>
      </v-sheet>
    </v-container>
    <p :class="'text text-wrap ' + themeColor" style="width: 80%; margin: 0 auto;" v-html="text"></p>
    <hr :class="themeColor"/>
  </v-container>
</template>

<script>
  import LineChart from './LineChart.js'

  export default {
    name: 'LineTrendsV2',
    components: {
      LineChart
    },
    data: () => ({
      options: {
        showLine: true,
        scales: {
          xAxes: [{
            display: true,
            scaleLabel: {
              display: true,
              labelString: 'Date'
            },
            type: 'time'
          }],
          yAxes: [{
            display: true,
            scaleLabel: {
              display: true,
              labelString: 'Number of Tweets'
            }
          }]
        }
      }
    }),
    props: ['title', 'text', 'themeColor', 'trend'],
    computed: {
      // a computed getter
      chartdata: function () {
        const posTrend = this.trend.pos
        const neuTrend = this.trend.neu
        const negTrend = this.trend.neg
        var posTrends = []
        var neuTrends = []
        var negTrends = []
        Object.keys(posTrend).forEach(function(key) {
            posTrends.push({ t: key, y: posTrend[key] })
        });
        Object.keys(neuTrend).forEach(function(key) {
            neuTrends.push({ t: key, y: neuTrend[key] })
        });
        Object.keys(negTrend).forEach(function(key) {
            negTrends.push({ t: key, y: negTrend[key] })
        });
        var chartdata = {
          labels: [],
          datasets: [
            {
              label: 'Positive',
              borderColor: '#77dd77',
              data: posTrends,
              fill: false
            },
            {
              label: 'Neutral',
              borderColor: '#779ECB',
              data: neuTrends,
              fill: false,
            },
            {
              label: 'Negative',
              borderColor: '#FF6961',
              data: negTrends,
              fill: false
            },
          ]
        }
        return chartdata
      }
    }
  }
</script>