<template>
  <v-container class="text-center">
    <v-container v-if="show == false">
    <p>This may take a few minutes</p>
    <v-progress-circular :size="70" :width="7" color="amber" indeterminate></v-progress-circular>
    </v-container>
    <!-- <v-lazy :options="{ threshold: .5 }" v-else> -->
    <div v-else>
    <ChartDonunt :title="overallOpinion.title" :text="overallOpinion.text" :themeColor="overallOpinion.themeColor" :posneuneg="opinion_overall.count_list"/>
    <LineSentiment :title="overallTrend.title" :text="overallTrend.text" :themeColor="overallTrend.themeColor" :trend="trends"/>
    <v-container class="text-center">
      <h3 class='text display-1 font-weight-bold'>Tweet's Topics</h3>
    <v-row style="margin: auto;">
      <v-col v-for="item in Object.keys(words)"
                :key="item"
                cols="sm" class="text-center">
      <h4 class="headline" v-if="item == 'pos'">Positive</h4>
      <h4 class="headline" v-if="item == 'neu'">Neutral</h4>
      <h4 class="headline" v-if="item == 'neg'">Negative</h4>
      <cloud :data="words[item]" nameKey="text" valueKey="value" :showTooltip="true" font="Sarabun" spiral='rectangular' :rotate="{from: 0, to: 0, numOfOrientation: 0}" v-if="item == 'pos'" :color="posColor"/>
      <cloud :data="words[item]" nameKey="text" valueKey="value" :showTooltip="true" font="Sarabun" spiral='rectangular' :rotate="{from: 0, to: 0, numOfOrientation: 0}" v-if="item == 'neu'" :color="neuColor"/>
      <cloud :data="words[item]" nameKey="text" valueKey="value" :showTooltip="true" font="Sarabun" spiral='rectangular' :rotate="{from: 0, to: 0, numOfOrientation: 0}" v-if="item == 'neg'" :color="negColor"/>
      </v-col>
    </v-row>
    <p :class="'text text-wrap ' + themeColor" style="width: 80%; margin: 0 auto;" v-html="tweetsText"></p>
    <hr :class="overallOpinion.themeColor"/>
    <h3 class='text display-1 font-weight-bold'>Hashtags</h3>
    <v-row style="margin: auto;">
      <v-col v-for="item in Object.keys(hashtags)"
                :key="item"
                md='4' sm="12" class="text-center">
      <h4 class="headline" v-if="item == 'pos'">Positive</h4>
      <h4 class="headline" v-if="item == 'neu'">Neutral</h4>
      <h4 class="headline" v-if="item == 'neg'">Negative</h4>
      <cloud :data="hashtags[item]" nameKey="text" valueKey="value" :showTooltip="true" font="Sarabun" spiral='rectangular' :rotate="{from: 0, to: 0, numOfOrientation: 0}" v-if="item == 'pos'" :color="posColor"/>
      <cloud :data="hashtags[item]" nameKey="text" valueKey="value" :showTooltip="true" font="Sarabun" spiral='rectangular' :rotate="{from: 0, to: 0, numOfOrientation: 0}" v-if="item == 'neu'" :color="neuColor"/>
      <cloud :data="hashtags[item]" nameKey="text" valueKey="value" :showTooltip="true" font="Sarabun" spiral='rectangular' :rotate="{from: 0, to: 0, numOfOrientation: 0}" v-if="item == 'neg'" :color="negColor"/>
      </v-col>
    </v-row>
    <p :class="'text text-wrap ' + themeColor" style="width: 80%; margin: 0 auto;" v-html="hashtagsText"></p>
    <hr :class="overallOpinion.themeColor"/>
    <v-row>
      <v-col col="12" md="8">
      </v-col>
      <v-col col='6' md='4'>
        <p style="color: #787878; font-style: italic;">Last updated: {{ opinion_overall.timestamp }}</p>
      </v-col>
    </v-row>
    </v-container>
    </div>
    <!-- </v-lazy> -->
  </v-container>
</template>
<script>
import ChartDonunt from './Analysis/ChartDonunt'
import LineSentiment from './Analysis/LineSentiment'
import Cloud from 'vue-wordcloud'
import { db } from '../utils/DB'

export default {
  name: 'Opinion',
  components: {
    ChartDonunt,
    LineSentiment,
    Cloud
  },
  data: () => ({
    overallOpinion: {
      title: 'Opinions Sentiment',
      text: '',
      themeColor: 'no-color'
    },
    overallTrend: {
      title: 'Overall Trend ',
      text: '',
      themeColor: 'no-color'
    },
    opinion_overall: null,
    trends: null,
    words: null,
    hashtags: null,
    tweetsText: '',
    hashtagsText: '',
    posColor: ['#008000', '#03C03C', '#77DD77', '#ACE1AF', '#B2EC5D'],
    neuColor: ['#08457E', '#1560BD', '#007FFF', '#5B92E5', '#A1CAF1'],
    negColor: ['#B31B1B', '#CE1620', '#FF0000', '#FF5C5C', '#F4C2C2'],
    opinion: null,
    show: false
  }),
  created () {
    this.$rtdbBind('opinion', db.ref('opinion')).then(() => {
        this.opinion_overall = this.opinion.overall
        this.trends = this.opinion.trend_sentiment
        this.words = this.opinion.words
        this.hashtags = this.opinion.hashtags
      })
      .catch((error) => {
        console.log('error in loading: ', error)
      })
  },
  mounted () {
    this.show = true
  }
}
</script>