<template>
  <v-container :class="'text-center ' + themeColor">
    <h3 :class="'text display-1 font-weight-bold ' + themeColor">{{ title }}</h3>
    <v-container>
      <v-sheet
        class="mx-auto"
        max-width="500"
      >
      <line-chart :chartdata="chartdata" :options="options"></line-chart>
      </v-sheet>
    </v-container>
    <p :class="'text text-wrap ' + themeColor" style="width: 80%; margin: 0 auto;" v-html="text"></p>
    <hr :class="themeColor"/>
  </v-container>
</template>

<script>
  import LineChart from './LineChart.js'

  export default {
    name: 'LineSentiment',
    components: {
      LineChart
    },
    data: () => ({
      options: {
        showLine: true,
        scales: {
          xAxes: [{
            display: true,
            scaleLabel: {
              display: true,
              labelString: 'Datetime'
            },
            type: 'time',
          }],
          yAxes: [{
            display: true,
            scaleLabel: {
              display: true,
              labelString: 'Sentiment'
            }
          }]
        }
      }
    }),
    props: ['title', 'text', 'themeColor', 'trend'],
    computed: {
      // a computed getter
      chartdata: function () {
        const posTrend = this.trend.pos
        const neuTrend = this.trend.neu
        const negTrend = this.trend.neg
        var chartdata = {
          labels: [],
          datasets: [
            {
              label: 'Positive',
              borderColor: '#77dd77',
              data: posTrend.sort((a, b) => (a.t > b.t) ? 1 : -1),
              fill: false
            },
            {
              label: 'Neutral',
              borderColor: '#779ECB',
              data: neuTrend.sort((a, b) => (a.t > b.t) ? 1 : -1),
              fill: false,
              hidden: true
            },
            {
              label: 'Negative',
              borderColor: '#FF6961',
              data: negTrend.sort((a, b) => (a.t > b.t) ? 1 : -1),
              fill: false
            },
          ]
        }
        return chartdata
      }
    }
  }
</script>