<template>
  <v-container :class="'text-center ' + themeColor">
    <h3 :class="'text display-1 font-weight-bold ' + themeColor">{{ title }}</h3>
    <v-container>
      <v-sheet
        class="mx-auto"
        max-width="400"
      >
      <bar-chart :chartdata="chartdata" :options="options"></bar-chart>
      </v-sheet>
    </v-container>
    <p :class="'text text-wrap ' + themeColor" style="width: 80%; margin: 0 auto;" v-html="text"></p>
    <hr :class="themeColor"/>
  </v-container>
</template>

<script>
  import BarChart from './BarChart.js'

  export default {
    components: {
      BarChart
    },
    data: () => ({
      options: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
            yAxes: [{
                ticks: {
                    beginAtZero: true,
                },
                categoryPercentage: 0.5
            }]
        },
        legend: {
            display: false,
        }
      }
    }),
    props: ['title', 'text', 'themeColor', 'posneuneg'],
    computed: {
      // a computed getter
      chartdata: function () {
        var dataChart = {
          labels: ['News', 'Ads', 'Opinions'],
          datasets: [
            {
              backgroundColor: ['#de7119', 'gray','#116979'],
              data: [parseInt(this.posneuneg.split(' ')[0]), parseInt(this.posneuneg.split(' ')[1]), parseInt(this.posneuneg.split(' ')[2])]
            }
          ]
        }
        return dataChart
      }
    }
  }
</script>