<template>
  <v-container class="text-center">
    <v-container v-if="show == false">
    <p>This may take a few minutes</p>
    <v-progress-circular :size="70" :width="7" color="amber" indeterminate></v-progress-circular>
    </v-container>
    <!-- <v-lazy v-else> -->
    <div v-else>
        <v-row>
            <v-col md='8' sm="12">
                <TwoBar :title="ads.title" :text="ads.text" :themeColor="ads.themeColor" :posneg="adsDb.count_list" labels="Mask Alcohol" :bgColor="['#de7119', '#116979']"/>
            </v-col>
            <v-col md='4' sm='12'>
                <NumberWindow :overallCounter="adsDb.all_ads" :maskCounter="adsDb.count_list.split(' ')[0]" :alcoholCounter="adsDb.count_list.split(' ')[1]"/>
            </v-col>
        </v-row>
        <v-row style="margin: auto;">
            <v-col col='sm' class="text-center">
              <h3 class='text display-1 font-weight-bold'>Ads's Topics</h3>
              <cloud :data="words" nameKey="text" valueKey="value" :showTooltip="true" font="Sarabun" spiral='rectangular' :rotate="{from: 0, to: 0, numOfOrientation: 0}"/>
            </v-col>
        </v-row>
        <p :class="'text text-wrap ' + 'no-color'" style="width: 80%; margin: 0 auto;" v-html="wordsText"></p>
        <hr :class="'no-color'"/>
        <v-row>
            <v-col col="12" md="8">
            </v-col>
            <v-col col='6' md='4'>
              <p style="color: #787878; font-style: italic;">Last updated: {{ adsDb.timestamp }}</p>
            </v-col>
        </v-row>
    </div>
    <!-- </v-lazy> -->
  </v-container>
</template>
<script>
import TwoBar from './Analysis/TwoBar'
import NumberWindow from './Analysis/NumberWindow'
import { db } from '../utils/DB'
import Cloud from 'vue-wordcloud'

export default {
  name: 'Ads',
  components: {
    TwoBar,
    NumberWindow,
    Cloud
  },
  data: () => ({
    ads: {
      title: 'Mask VS Alcohol',
      text: '',
      themeColor: 'no-color'
    },
    adsDb: null,
    words: null,
    wordsText: '',
    adsObj: null,
    show: false
  }),
  firebase: {
    adsDb: db.ref('ads/counter'),
    words: db.ref('ads/words')
  },
  created () {
    this.$rtdbBind('adsObj', db.ref('ads')).then(() => {
        this.adsDb = this.adsObj.counter
        this.words = this.adsObj.words
        this.show = true
      })
      .catch((error) => {
        console.log('error in loading: ', error)
      })
  },
}
</script>