<template>
  <v-container :class="'text-center ' + themeColor">
    <h3 :class="'text display-1 font-weight-bold ' + themeColor">{{ title }}</h3>
    <v-container>
      <v-sheet
        class="mx-auto"
        max-width="500"
      >
      <donunt :chartdata="chartdata" :options="options"></donunt>
      </v-sheet>
    </v-container>
    <p :class="'text text-wrap ' + themeColor" style="width: 80%; margin: 0 auto;" v-html="text"></p>
    <hr :class="themeColor"/>
  </v-container>
</template>

<script>
  import Donunt from './Donunt.js'

  export default {
    name: 'DonuntChart',
    components: {
      Donunt
    },
    data: () => ({
      options: {
        animation: {
          animateRotate: true
        }
      }
    }),
    props: ['title', 'text', 'themeColor', 'posneuneg'],
    computed: {
      // a computed getter
      chartdata: function () {
        var chartdata = {
            labels: ['Positive', 'Neutral', 'Negative'],
            datasets: [
              {
                backgroundColor: ['#77dd77', '#779ECB', '#FF6961'],
                data: [parseInt(this.posneuneg.split(' ')[0]), parseInt(this.posneuneg.split(' ')[1]), parseInt(this.posneuneg.split(' ')[2])]
              },
            ]
          }
        return chartdata
      }
    }
  }
</script>