<template>
  <v-container class="text-center">
    <v-container v-if="show == false">
    <p>This may take a few minutes</p>
    <v-progress-circular :size="70" :width="7" color="amber" indeterminate></v-progress-circular>
    </v-container>
    <!-- <v-lazy v-else :options="{ threshold: .5 }" > -->
    <div v-else>
    <TwoBar :title="overallNews.title" :text="overallNews.text" :themeColor="overallNews.themeColor" :posneg="news_overall.count_list" labels="Positive Negative" :bgColor="['#77dd77', '#FF6961']"/>
    <StackBar :title="sourceNews.title" :text="sourceNews.text" :themeColor="sourceNews.themeColor" v-bind:sources="news_sources"/>
    <h3 class='text display-1 font-weight-bold'>News Headlines</h3>
      <v-row style="margin: auto;">
        <v-col v-for="item in Object.keys(words)"
                  :key="item"
                  md='6' sm="12" class="text-center">
        <h4 class="headline" v-if="item == 'pos'">Positive</h4>
        <h4 class="headline" v-if="item == 'neg'">Negative</h4>
        <cloud :data="words[item]" nameKey="text" valueKey="value" :showTooltip="true" font="Sarabun" spiral='rectangular' :rotate="{from: 0, to: 0, numOfOrientation: 0}" v-if="item == 'pos'" :color="posColor"/>
        <cloud :data="words[item]" nameKey="text" valueKey="value" :showTooltip="true" font="Sarabun" spiral='rectangular' :rotate="{from: 0, to: 0, numOfOrientation: 0}" v-if="item == 'neg'" :color="negColor"/>
        </v-col>
      </v-row>
    <p :class="'text text-wrap ' + 'no-color'" style="width: 80%; margin: 0 auto;" v-html="newsText"></p>
    <hr :class="'no-color'"/>
    <v-row>
      <v-col col="12" md="8">
      </v-col>
      <v-col col='6' md='4'>
        <p style="color: #787878; font-style: italic;">Last updated: {{ news_overall.timestamp }}</p>
      </v-col>
    </v-row>
    </div>
    <!-- </v-lazy> -->
  </v-container>
</template>
<script>
import TwoBar from './Analysis/TwoBar'
import StackBar from './Analysis/StackBar'
import { db } from '../utils/DB'
import Cloud from 'vue-wordcloud'

export default {
  name: 'News',
  components: {
    TwoBar,
    StackBar,
    Cloud
  },
  data: () => ({
    overallNews: {
      title: 'Sentiment in News',
      text: '',
      themeColor: 'no-color'
    },
    sourceNews: {
      title: 'Source Break Up',
      text: '',
      themeColor: 'no-color'
    },
    news_overall: null,
    news_sources: null,
    words: null,
    newsText: '',
    posColor: ['#008000', '#03C03C', '#77DD77', '#ACE1AF', '#B2EC5D'],
    negColor: ['#B31B1B', '#CE1620', '#FF0000', '#FF5C5C', '#F4C2C2'],
    news: null,
    show: false
  }),
  created () {
    this.$rtdbBind('news', db.ref('news')).then(() => {
        this.news_overall = this.news.overall
        this.news_sources = this.news.source
        this.words = this.news.words
      })
      .catch((error) => {
        console.log('error in loading: ', error)
      })
  },
  mounted () {
    this.show = true
  }
}
</script>
<style>
@import url('https://fonts.googleapis.com/css2?family=Sarabun&display=swap');
div.tooltip {
    position: absolute;
    width: 150px !important;
    height: auto !important;
    padding: 8px;
    font: 12px !important;
    font-family: 'Sarabun', sans-serif !important;
    line-height: 20px !important;
    color: white;
    background: black;
    border: 0px;
    border-radius: 2px;
    pointer-events: none;
}
</style>