<template>
  <v-container :class="'text-center ' + themeColor">
    <h3 :class="'text display-1 font-weight-bold ' + themeColor">{{ title }}</h3>
    <v-container>
      <v-sheet
        class="mx-auto"
        max-width="500"
      >
      <line-chart :chartdata="chartdata" :options="options"></line-chart>
      </v-sheet>
    </v-container>
    <p :class="'text text-wrap ' + themeColor" style="width: 80%; margin: 0 auto;" v-html="text"></p>
    <hr :class="themeColor"/>
  </v-container>
</template>

<script>
  import LineChart from './LineChart.js'

  export default {
    name: 'LineTrends',
    components: {
      LineChart
    },
    data: () => ({
      options: {
        showLine: true,
        scales: {
          xAxes: [{
            display: true,
            scaleLabel: {
              display: true,
              labelString: 'Date'
            },
            type: 'time'
          }],
          yAxes: [{
            display: true,
            scaleLabel: {
              display: true,
              labelString: 'Number of Tweets'
            }
          }]
        }
      }
    }),
    props: ['title', 'text', 'themeColor', 'trend'],
    computed: {
      // a computed getter
      chartdata: function () {
        const posTrend = this.trend.news
        const neuTrend = this.trend.ads
        const negTrend = this.trend.opinion
        var newsTrends = []
        var adsTrends = []
        var opiTrends = []
        Object.keys(posTrend).forEach(function(key) {
            newsTrends.push({ t: key, y: posTrend[key] })
        });
        Object.keys(neuTrend).forEach(function(key) {
            adsTrends.push({ t: key, y: neuTrend[key] })
        });
        Object.keys(negTrend).forEach(function(key) {
            opiTrends.push({ t: key, y: negTrend[key] })
        });
        var chartdata = {
          labels: [],
          datasets: [
            {
              label: 'News',
              borderColor: '#de7119',
              data: newsTrends,
              fill: false
            },
            {
              label: 'Ads',
              borderColor: 'gray',
              data: adsTrends,
              fill: false,
            },
            {
              label: 'Opinion',
              borderColor: '#116979',
              data: opiTrends,
              fill: false
            },
          ]
        }
        return chartdata
      }
    }
  }
</script>